<template>
    <div class="app flex-column">
        <div class="prize-list flex-column-grow" v-if="sscTimeList">
            <div class="mui-scroll">
                <ul>
                    <template v-for="item in sscTimeList">
                        <li :key="item.playGroupId" v-if="item.enable" :data-id="item.playGroupId">
                            <router-link :to="'/prizeList?id=' + item.playGroupId + '&name=' + item.playGroupName">
                                <div class="fl-layout">
                                    <h2>{{item.playGroupName}}<p>{{item.number}}期 　{{item.lastOpenTime | timer}}</p></h2>
                                    <div class="number" v-if="item.lastOpenData">
                                        <div class="number-component" v-if="playHandle(item.playGroupId)">
                                            <component :image="true" v-bind:is="'OpenCode-' + item.playGroupId" :number="item.lastOpenData.split(',')"></component>
                                        </div>
                                        <div v-else>
                                            <span v-for="(item1, index) in item.lastOpenData.split(',')" :key="index">{{item1}}</span>
                                        </div>
                                    </div>
                                    <div class="number" v-else>
                                        <p>正在开奖...</p>
                                    </div>
                                </div>
                                <div class="fr-layout"></div>
                            </router-link>
                        </li>
                    </template>
                </ul>
            </div>
        </div>
        <loadding v-else></loadding>
    </div>
</template>

<script>
import { mapState } from "vuex";
export default {
    data(){
        return {
            
        }
    },
    created(){
        this.getNetData();       
    },
    mounted(){
        this.test();
    },
    computed: {
        ...mapState('Game', {
            sscTimeList: state=> state.getAllSscOpenTime.sscTimeList
        })
    },
    methods:{
        test(){
            var is = 'OpenCode-3'
            console.log(this.$root.$options);
        },
        playHandle(id){
            let arr = [14 , 15 , 23 , 27 , 29, 20, 9, 22, 30, 31, 32, 6, 18, 19]
            return arr.contains(id)
        },
        getNetData(){
            // this.$api.game.getAllSscOpenTime()
            this.$store.dispatch('Game/getAllSscOpenTime')
            .then(res=>{
                this.mui_scroll('.prize-list');
            })    
        },
    }
}
</script>

